	module.exports = "<h2 id=overview>Overview</h2> <p>Always add a modifier class to add color to the button.</p> <h2 id=accessibility>Accessibility</h2> <p>An active state buttons will appear pressed when active. There’s no need to add a class to <code>&lt;button&gt;</code>s as they use a pseudo-class. However, you can still force the same active appearance with <code>.pf-is-active</code> (and include the <code>aria-pressed=&quot;true&quot;</code> attribute) should you need to replicate the state programmatically.</p> <p>Disabled buttons should include the <code>aria-disabled=&quot;true&quot;</code> attribute to indicate the state of the element to assistive technologies.</p> <p>The <code>.pf-is-disabled</code> class uses <code>pointer-events: none</code> to try to disable the link functionality of <code>&lt;a&gt;</code>s, but that CSS property is not yet standardized. In addition, even in browsers that do support <code>pointer-events: none</code>, keyboard navigation remains unaffected, meaning that sighted keyboard users and users of assistive technologies will still be able to activate these links. So to be safe, add a <code>tabindex=&quot;-1&quot;</code> attribute on these links (to prevent them from receiving keyboard focus) and use custom JavaScript to disable their functionality.</p> <h2 id=usage>Usage</h2> <p>| Class | Usage | | <code>.pf-c-button</code> <strong>Applied to:</strong> <code>&lt;a&gt;</code>, <code>&lt;button&gt;</code> | <strong>Outcome:</strong> Initiates a button <strong>Required:</strong> Yes <strong>Remarks:</strong> Always use it with a modifier class. | | <code>.pf-is-primary</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Modifies for primary styles <strong>Required:</strong> No | | <code>.pf-is-secondary</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Modifies for secondary styles <strong>Required:</strong> No | | <code>.pf-is-tertiary</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Modifies for tertiary styles <strong>Required:</strong> No | | <code>.pf-is-danger</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Modifies for tertiary styles <strong>Required:</strong> No | | <code>.pf-is-block</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Creates a block level button <strong>Required:</strong> No | | <code>.pf-is-small</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Creates a small button <strong>Required:</strong> No | | <code>.pf-is-small</code> <strong>Applied to:</strong> <code>.pf-c-button</code> | <strong>Outcome:</strong> Creates a large button <strong>Required:</strong> No |</p> ";

